@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  background: #ffffff;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card {
    position: relative;
    margin: 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 6px;
    padding: 0rem;
    overflow: hidden;
    background: white;
  }
  
.homeCard {
  height: 100%;
}
.homeCardTop {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  text-align: center;
  color: white;
}
.homeCardTop img {
  width: 100%;
}

.wordInImage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: larger;
  font-weight: bold;
}

.homeCardBottom {
  padding-left: 3rem;
  padding-right: 3rem;
}

.cardLink a {
  text-decoration: none;
  color: black;
}

.cardLink {
    height: 100%;
}


@media (min-width: 768px) {
  .homeCard {
    height: 100%;
  }
  .homeCardTop img {
    width: 100%;
    height: 15rem;
    object-fit: cover;
  }
}

.homeHeader {
  background-image: url(/PersonalResumeWebsite/static/media/headerImage.c1286f3a.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  text-align: center;
  padding-top: 150px;
  padding-bottom: 150px;
  color: white;
  /* margin-bottom: 2em; */
}

.homeMain {
  display: block;
  padding: 1rem;
  height: 25rem;
}

.homeMain .homeMainLeft {
  float: none;
  margin-bottom: 10px;
  text-align: center;
}

.homeMain .homeMainCenter {
  float: none;
  margin-bottom: 10px;
  text-align: center;
}
.homeMain .homeMainRight {
  float: none;
  margin-bottom: 10px;
  text-align: center;
}

@media (min-width: 768px) {
  .homeMain {
    display: block;
  }

  .homeMain .homeMainLeft {
    float: left;
    width: 30%;
    margin-bottom: 50px;
    text-align: center;
    margin-left: 3%;
    height: 100%;
  }

  .homeMain .homeMainCenter {
    float: left;
    width: 30%;
    margin-bottom: 50px;
    text-align: center;
    margin-left: 2%;
    margin-right: 2%;
    height: 100%;
  }
  .homeMain .homeMainRight {
    float: right;
    width: 30%;
    margin-bottom: 50px;
    text-align: center;
    margin-right: 3%;
    height: 100%;
  }
}

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #ffffff;
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.2);
  padding: 0 1rem;
  z-index: 5;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:active,
.nav-links a.active {
  background: black;
  border-radius: 0.15rem;
  color: white;
}

.nav-links a:hover,
.nav-links a.hover {
  background: #e0b09374;
  color: #292929;
  border-radius: 0.15rem;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

/* Dropdown Button */
.dropbtn {
  background-color: #4caf50;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: black;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }

  /* Dropdown Button */
  .dropbtn {
    background-color: #4caf50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;
  }

  /* The container <div> - needed to position the dropdown content */
  .dropdown {
    position: relative;
    display: inline-block;
  }

  .dropdown .dropbtn {
    color: black;
  }

  /* Dropdown Content (Hidden by Default) */
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }

  /* Links inside the dropdown */
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  /* Change color of dropdown links on hover */
  .dropdown-content a:hover {
    background-color: #ddd;
  }

  /* Show the dropdown menu on hover */
  .dropdown:hover .dropdown-content {
    display: block;
  }

  /* Change the background color of the dropdown button when the dropdown content is shown */
  .dropdown:hover .dropbtn {
    background: #e0b09374;
    color: #292929;
    border-radius: 0.15rem;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 30%;
  background: #D3D3D3;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: black;
}

.main-navigation__title {
  color: black;
}

.main-navigation__title a {
  text-decoration: none;
  color: black;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

